import Options from './options';
import Content from './content';

export default class extends React.Component{	
	constructor(props) {
	    super(props)
	    this.state = { 
	    	itemsPerPage: 16,	    	
	    	currentPageNumber:1,
	    	actualItems: 16
	    }
	    this.handleClick = this.handleClick.bind(this);
	}

	handleClick(event) {
        this.setState({
          currentPageNumber: Number(event.target.id)
        });
    }

	render(){
		const { products } = this.props
		const { currentPageNumber, actualItems, itemsPerPage } = this.state		
		
		// paginado
		const totalProducts = products.length
		const indexOfLastProducts = currentPageNumber * itemsPerPage;
        const indexOfFirstProducts = indexOfLastProducts - itemsPerPage;
        const currentProducts = products.slice(indexOfFirstProducts, indexOfLastProducts);
		
	    return (
	        <div className="container">       	
	    		<Options section="header" actual={actualItems} total={totalProducts}></Options>
	        	<Content 
	        		currentProducts={currentProducts}		
	        	>
	        	</Content>
	            <Options section="footer" actual={actualItems} total={totalProducts}></Options>
	        </div>
    	);	
	}	
}


